





















export default {
  data:function() {
    return {
        value:''
      };
    },
  props: {
    placeholder: {
      type: String,
      default: ""
    },
    autocomplete: {
      type: String,
      default: "off"
    },
  },
  methods:{
    changeInput:function(e){
      this.value = e.target?.value || ''
      this.$emit('onInput',this.value)
    },
    handleClose:function(){
      this.value = ''
      this.$emit('onInput', '')
    }
  }
};
