

































import Vue from "vue";
import {toDataTime} from '../../util/index'

export default Vue.extend({
  name: "Comment",
  props: {
    dataSource: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {};
  },
  created() {
    console.log(this, "created");
  },
  methods: {
    comment: function(item) {
      this.$emit("comment", item);
    },
    transTime:function(val){
        return toDataTime(val)
    }
  }
});
