































import Vue from "vue";
export default Vue.extend({
  props: {
  },
  components: {},
  data() {
    return {
      item: {}
    };
  },
  mounted() {
    this.item = JSON.parse(this.$route.query.item);
    console.log(this);
  },
  filters: {
    count(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else {
        return (value / 10000).toFixed(1) + "万";
      }
    }
  }
});
